import './App.css'
import React, { useState } from 'react'
import isElectron from 'is-electron'
const { ipcRenderer } = window.require("electron")
const path = require('path')
const jp = require('jsonpath')
const _ = require('lodash');
//const electron = window.require('electron');

//const {BrowserWindow} = remote


function App() {


  const [config, setConfig] = useState(ipcRenderer.sendSync('GET-CONFIG', ''))
  const [source, setSource] = useState(config.IMAGE_DIR.source)

  function run(e) {
    e.preventDefault();


    //config.IMAGE_DIR.source = e.value
    ipcRenderer.sendSync('RUN', config)

  }

  function openFolderDialog(defoultPathName,getfile,e) {
    e.preventDefault()
    let defoultPath =  _.get(config, defoultPathName)
    let sendData = {defaultPath: defoultPath, properties: ['openDirectory','dontAddToRecent' ] }
    if(getfile==true) sendData = {defaultPath: defoultPath, properties: ['openFile','dontAddToRecent' ] }
    let path = ipcRenderer.sendSync('OPEN-FOLDER-DIALOG', sendData)
    console.log("path",path)
    if(path!=undefined)  _.set(config, defoultPathName, path[0])
 
    setConfig({ ...config })

  }



  return (
    <div className="App">

      <header className="App-header">

        <div>
          <div style={{marginTop: "30px"}}>IMAGE_DIR_SOURCE</div>
          <input type='text' style={{ width: '390px' ,fontSize:'15px' }} placeholder="Please input path" value={path.join(config.IMAGE_DIR.source)} onChange={e => {config.IMAGE_DIR.source=e.target.value; setConfig({ ...config })} } />
          <button style={{ width: '30px' ,fontSize:'16px' }}  onClick={e=>openFolderDialog("IMAGE_DIR.source",false,e)}>...</button>
          <div style={{marginTop: "30px"}}>IMAGE_DIR_DESTINATION</div>
          <input type='text' style={{ width: '390px',fontSize:'15px' }} placeholder="Please input path" value={path.join(config.IMAGE_DIR.destination)} onChange={e => {config.IMAGE_DIR.destination = e.target.value; setConfig({ ...config })}} />
          <button style={{ width: '30px' ,fontSize:'16px' }}  onClick={e=>openFolderDialog("IMAGE_DIR.destination",false,e)} >...</button>
          <div style={{marginTop: "30px"}}>GRID_PATH</div>
          <input type='text' style={{ width: '390px',fontSize:'15px' }} placeholder="Please input path" value={path.join(config.GRID_PATH)} onChange={e => {config.GRID_PATH = e.target.value; setConfig({ ...config })}} />
          <button style={{ width: '30px' ,fontSize:'16px' }}  onClick={e=>openFolderDialog("GRID_PATH",true,e)}>...</button>
        </div>
        <div>
          <button style={{margin:'85px', width: '100px',height:'50px' }} onClick={run} >RUN</button>
        </div>
      </header>
    </div>
  );
}

export default App;
